* {
  padding: 0;
  margin: 0;
  background-color: rgb(25, 25, 25);
  color: whitesmoke;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

header {
  margin: 30px auto;
  text-align: center;
  text-transform: uppercase;
}

.para {
  text-align: center;
  justify-content: center;
  margin: 30px auto;
  max-width: 700px;
  font-size: 20px;
}

footer {
  font-size: 15px;
  text-align: center;
  margin: 100px;
}

@media screen and (max-width: 768px) {
  .para {
    text-align: center;
    justify-content: center;
    margin: 30px auto;
    max-width: 300px;
    font-size: 20px;
  }
}